@font-face {
	font-family: "automatewoo-referrals-icons";
	src: url("../fonts/automatewoo-referrals-iconfont.eot");
	src: url("../fonts/automatewoo-referrals-iconfont.eot?#iefix") format("embedded-opentype"),
	url("../fonts/automatewoo-referrals-iconfont.woff") format("woff"),
	url("../fonts/automatewoo-referrals-iconfont.ttf") format("truetype"),
	url("../fonts/automatewoo-referrals-iconfont.svg#Flaticon") format("svg");
	font-weight: normal;
	font-style: normal;
}

@media screen and (-webkit-min-device-pixel-ratio: 0) {
	@font-face {
		font-family: "automatewoo-referrals-icons";
		src: url("../fonts/automatewoo-referrals-iconfont.svg#Flaticon") format("svg");
	}
}

%automatewoo-referrals-icon {
	display: inline-block;
	font-family: "automatewoo-referrals-icons";
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	line-height: 1;
	text-decoration: inherit;
	text-rendering: optimizeLegibility;
	text-transform: none;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	font-smoothing: antialiased;
}

%automatewoo-referrals-icon--mail {
	content: "\EA01";
}

%automatewoo-referrals-icon--twitter {
	content: "\EA03";
}

%automatewoo-referrals-icon--facebook {
	content: "\EA02";
}

%automatewoo-referrals-icon--whatsapp {
  content: "\EA04";
}
